.main {
  padding-top: 160px;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  margin-left: 30px;
}

.burger {
  margin-bottom: 40px;
}

.burgerTop {
  margin-bottom: 10px;
}

ul {
  overflow: auto;
  max-height: calc(100vh - 600px);
  list-style-type: none;
  margin: 0;
  padding: 0;
  margin-top: 16px;
}

.constructorItem {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.drag {
  height: 32px;
  margin-right: 13px;
  cursor: move;
}

.price {
  margin-right: 40px;
}
